@import url("https//fonts.googleapis.com/css?family=Work+Sans:regular,500,600&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

 :root {
  --bg-color: #121B25;
  --accent-color: #15786C;
  --text-color: #d6d6d6; }

body {
    background-color: var(--bg-color); }
[class*=__container] {
    max-width: 1450px;
    padding: 0 15px;
    margin: 0 auto; }

/* Header */

.header {
    padding: 40px auto;
    // .header__container
    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center; } }

